.historial-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    top: 40px; 
    backdrop-filter: blur(10px);
  }
  
  .historial-modal-content {
    position: relative;
    background-color: #121213bd;
    padding: 30px;
    border-radius: 10px;
    width: 80%;
    max-width: 1200px; /* Ajusta el tamaño máximo si es necesario */
    overflow: auto;
    height: 80%;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .historial-close-button {
    position: absolute;
    top: 0px; /* Ajusta la posición del botón de cierre */
    right: 0px; /* Ajusta la posición del botón de cierre */
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: #ffffff;
  }
  
  .historial-close-button:hover {
    color: #ff0000; /* Color del botón de cierre al pasar el mouse */
  }
  
  .historial-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .historial-item {
    position: relative;
    flex: 1 1 calc(33% - 20px); /* Ajusta el tamaño de las imágenes */
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 8px;
    transition: transform 0.3s ease, border 0.3s ease; /* Transición suave para el tamaño y borde */
  }
  
  .historial-item:hover,
  .historial-item.active {
    transform: scale(1.05); /* Aumenta el tamaño de la imagen al pasar el mouse */
    border: 3px solid rgba(253, 248, 248, 0.733); /* Borde al pasar el mouse */
  }
  
  .historial-item img {
    width: 100%;
    height: 150px;
    display: block;
    object-fit: cover;
    transition: transform 0.3s ease; /* Transición suave para la imagen */
    cursor: pointer;
  }
  
  .historial-item-info {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    padding: 10px;
    text-align: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    cursor: pointer;
  }
  
  .historial-item:hover .historial-item-info,
  .historial-item.active .historial-item-info {
    opacity: 1; /* Muestra la información al pasar el mouse o hacer clic */
  }
  
  .historial-item-description {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  
  .historial-item-name {
    font-size: 16px;
    font-weight: bold;
  }
  
  .historial-item-date {
    font-size: 14px;
  }
  